import React from 'react'
import {Field, reduxForm} from 'redux-form'
import '../../css/app.css';
import '../../css/edit-form.css';
import {renderedInputField} from "../customFormComponents/rendetedInputField";
import {required} from "../../validators/required";


const IncreaseOscarsForm = (props) => {
    const {handleSubmit, pristine, reset, submitting, id} = props

    return (
        <form onSubmit={handleSubmit}>
            <div className={"movie-form"}>
                <h3>          ""</h3>
                <Field name="duration" type="number"
                       component={renderedInputField} label=""
                       validate={[required]}
                />
                <Field name="oscars" type="number"
                       component={renderedInputField} label=" "
                       validate={[required]}
                />
                <div>
                    <button id="edit-form-but" disabled={submitting}>Submit</button>
                </div>
            </div>
        </form>
    )
}

export default reduxForm({
    form: 'increase-oscars-form',
    keepDirtyOnReinitialize: true
})(IncreaseOscarsForm)