import '../../css/filter-header.css';
import '../../css/app.css';
import {useHistory} from "react-router-dom";

function AdditionalTasksHeader() {
    const history = useHistory();

    function handleDurationClick(evt) {
        evt.preventDefault();
        history.push("/additional/duration");
    }

    function handleGenreClick(evt) {
        evt.preventDefault();
        history.push("/additional/genre");
    }

    function handleScreenwriterClick(evt) {
        evt.preventDefault();
        history.push("/additional/screenwriter");
    }

    function handleReallocateOscarsClick(evt) {
        evt.preventDefault();
        history.push("/additional/reallocate-oscars");
    }

    function handleIncreaseOscarsClick(evt) {
        evt.preventDefault();
        history.push("/additional/increase-oscars");
    }

    return (
        <div className="filter-header base">
            <div className={"tasks-div"}>
                <button id="new-movie-but" onClick={handleGenreClick}>
                    GENRE TASK
                </button>
                <button id="new-movie-but" onClick={handleDurationClick}>
                    DURATION TASK
                </button>
                <button id="new-movie-but" onClick={handleScreenwriterClick}>
                    SCREENWRITER TASK
                </button>
                <button id="new-movie-but" onClick={handleReallocateOscarsClick}>
                    REALLOCATE OSCARS
                </button>
                <button id="new-movie-but" onClick={handleIncreaseOscarsClick}>
                    INCREASE OSCARS
                </button>
            </div>
        </div>
    );
}

export default AdditionalTasksHeader;
