import '../../css/table-layout.css';
import {useDispatch, useSelector} from "react-redux";
import ReallocateOscarsForm from "./ReallocateOscarsForm";
import {reallocateOscarsRequest} from "../../actions/additionalTasksActions";


function ReallocateOscarsPage() {

    const dispatch = useDispatch();

    const onSubmit = (formData) => {
        dispatch(reallocateOscarsRequest(formData));
    };

    return (
        <div className="table-layout">
            <h2>  -</h2>
            <hr/>
            <ReallocateOscarsForm onSubmit={onSubmit}/>
        </div>
    );
}

export default ReallocateOscarsPage;
