import React, {useEffect} from 'react'
import {Field, reduxForm} from 'redux-form'
import '../../css/app.css';
import '../../css/edit-form.css';
import {add_genres} from "../../constants/enumConstants";


const ReallocateOscarsForm = (props) => {

    const {handleSubmit, pristine, reset, submitting, id} = props

    useEffect(() => {
        props.reset();
        props.initialize({
            genre_from: add_genres[0],
            genre_to: add_genres[0]
        });
    }, [])

    return (
        <form onSubmit={handleSubmit}>
            <div className={"movie-form"}>
                <h3>   "",    ,   
                    </h3>
                <div className="label">    </div>
                <Field name="genre_from"
                       component="select" label="Genre">
                    {add_genres.map(o => <option key={o} value={o}>{o} </option>)}
                </Field>
                <div className="label">  </div>
                <Field name="genre_to"
                       component="select" label="Genre">
                    {add_genres.map(o => <option key={o} value={o} >{o} </option>)}
                </Field>
                <div>
                    <button id="edit-form-but" disabled={submitting}>Submit</button>
                </div>
            </div>
        </form>
    )
}

export default reduxForm({
    form: 'reallocate-oscars-form',
    keepDirtyOnReinitialize: true
})(ReallocateOscarsForm)