import '../../css/table-layout.css';
import {useDispatch} from "react-redux";
import IncreaseOscarsForm from "./IncreaseOscarsForn";
import {increaseOscarsRequest} from "../../actions/additionalTasksActions";


function IncreaseOscarsPage() {

    const dispatch = useDispatch();

    const onSubmit = (formData) => {
        dispatch(increaseOscarsRequest(formData));
        console.log("HELLO");
    };

    return (
        <div className="table-layout">
            <h2>   !</h2>
            <hr/>
            <IncreaseOscarsForm onSubmit={onSubmit}/>
        </div>
    );
}

export default IncreaseOscarsPage;
